.thank-section {
	position: relative;
	.orn-1 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		@include max-lg {
			width: 150%;
		}
		.orn-2 {
			position: absolute;
			bottom: 0;
			left: 50%;
			width: 50%;
			transform: translate(-50%, 10%) rotate(-5deg);
			@include max-lg {
				transform: translate(-25%, 10%) rotate(-5deg);
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 7/8;
		margin: 0 auto;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: var(--border-radius);
			}
		}
	}
	.thank-wrapper {
		position: relative;
		padding: 5rem 0;
		text-align: center;
		overflow: hidden;
		@include max-md {
			padding: 3rem 0;
		}
		.thank {
			position: relative;
			padding: 3rem;
			@include max-md {
				padding: 0;
			}
			&::before {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background-color: var(--color-primary);
				border-radius: var(--border-radius);
				opacity: 0.5;
				content: "";
			}
			.thank-body {
				position: relative;
				width: 80%;
				margin: 2rem auto 0;
				@include max-md {
					width: 100%;
					padding: 1rem;
				}
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
