.venue-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.venue-wrapper {
		position: relative;
		z-index: 3;
		.venue-description {
			position: relative;
			margin: 0 auto 3rem;
			z-index: 2;
			text-align: left;
			@include max-lg {
				margin-bottom: 3rem;
			}
			.title-section {
				margin-left: 0;
				margin-right: 0;
				h2,
				span,
				p {
					padding-left: 0;
				}
			}
		}
		.venue-content {
			position: relative;
			@include max-md {
				.container {
					padding: 0;
				}
			}
			.ribbon-venue {
				position: relative;
				margin-bottom: 0.5rem;
				svg {
					width: 40px;
					height: auto;
					path {
						stroke: var(--color-dark);
					}
				}
				.st0 {
					fill: var(--color-secondary);
				}
				.st1 {
					fill: none;
					stroke: var(--color-secondary);
					stroke-width: 19;
					stroke-linecap: round;
					stroke-miterlimit: 10;
					transform-origin: 50% 75%;
				}
			}
			.card {
				background-color: transparent;
				z-index: 5;
				text-align: left;
				border: none;
				border-radius: 0;
				.card-body {
					position: relative;
					padding: 3rem;
					@include max-md {
						padding: 2rem;
					}
					h4 {
						margin: 0 0 1rem;
					}
					hr {
						display: block;
						width: 80%;
						height: 2px;
						margin: 0 0 1rem;
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin: 3rem 0 0;
		position: relative;
		padding: 2rem;
		text-align: center;
		z-index: 2;
		.title-section {
			&::before {
				all: unset;
			}
		}
		@include max-md {
			padding: 2rem 0;
		}
		* {
			position: relative;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue {
		row-gap: 3rem;
	}
	.event-name {
		position: relative;
		width: 100%;
		.date {
			position: relative;
			width: 100%;
		}
		h4 {
			font-family: $font-secondary;
		}
		p {
			margin-bottom: 0;
			font-weight: 600;
		}
	}
	.event-description {
		position: relative;
		padding: 1rem;
		box-shadow: 0 1rem 1rem rgba($color: #000000, $alpha: 0.15);
		margin: 1rem 0;
		border-radius: var(--border-radius);
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			border-radius: var(--border-radius);
			background-color: var(--color-primary);
			opacity: 0.5;
			content: "";
		}
		p {
			margin-bottom: 0;
		}
	}
	.event-place {
		position: relative;
		border-radius: var(--border-radius);
		background-color: var(--color-primary);
		box-shadow: 0 1rem 1rem rgba($color: #000000, $alpha: 0.15);
		padding: 1rem;
		p {
			margin-bottom: 0;
		}
	}
	.widget-elements {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 100%;
		transform: translate(-50%, 50%);
		display: flex;
		justify-content: center;
		.btn-custom {
			margin: 0 auto;
		}
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
