.story-section {
	position: relative;
	.story {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
	}
	.story-content {
		position: relative;
		padding: 5rem 3rem 3rem;
		@include max-md {
			padding: 0;
		}
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background-color: var(--color-primary);
			border-radius: var(--border-radius);
			opacity: 0.5;
			content: "";
		}
	}
	.story-description {
		position: relative;
		text-align: center;
		@include max-md {
			padding: 1rem;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 7/8;
		margin-bottom: 3rem;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
				border-radius: var(--border-radius);
			}
		}
	}
}
