.stream-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.stream-wrapper {
		position: relative;
		.stream-content {
			position: relative;
			.title-section {
				margin-left: 0;
				margin-right: 0;
				h2,
				p,
				span {
					padding-left: 0;
				}
			}
			.stream-description {
				position: relative;
				padding: 3rem 2rem;
				border-top-left-radius: var(--border-radius);
				border-top-right-radius: var(--border-radius);
				background-color: rgba($color: #1e1f1e, $alpha: 0.5);
				@include max-md {
					padding: 2rem 1rem;
				}
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		position: relative;
		width: 100%;
		color: var(--color-light);
	}
}
