.rsvp-section {
	position: relative;
	padding: 5rem 0;
	overflow: hidden;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		@include max-lg {
			width: 150%;
		}
		.orn-2 {
			position: absolute;
			bottom: 0;
			left: 50%;
			width: 50%;
			transform: translate(-50%, 10%) rotate(-5deg);
			@include max-lg {
				transform: translate(-25%, 10%) rotate(-5deg);
			}
		}
	}
	.rsvp-wrapper {
		position: relative;
		overflow: hidden;
		border-radius: var(--border-radius);
		box-shadow: 0 1rem 1rem rgba($color: #000000, $alpha: 0.15);
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background-color: var(--color-primary);
			opacity: 0.5;
			border-radius: var(--border-radius);
			content: "";
		}
		.rsvp-form {
			position: relative;
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
			.title-section {
				&::before {
					all: unset;
				}
			}
			.form-control,
			.form-select {
				background-color: rgba($color: #fff, $alpha: 0.7);
			}
		}
		.image-wrapper {
			position: relative;
			width: 100%;
			height: auto;
			aspect-ratio: 7/8;
			.rsvp-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
}
