.header-section {
	position: relative;
	overflow: hidden;
	width: 100%;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		@include max-lg {
			width: 150%;
		}
		.orn-2 {
			position: absolute;
			bottom: 0;
			left: 50%;
			width: 50%;
			transform: translate(-50%, 10%) rotate(-5deg);
			@include max-lg {
				transform: translate(-25%, 10%) rotate(-5deg);
			}
		}
	}
	.header-content {
		position: relative;
		min-height: 100vh;
		.header {
			position: relative;
			width: 50%;
			height: 100%;
			margin: 0 auto;
			padding: 4.5rem 0 0;
			@include max-lg {
				width: 80%;
			}
			@include max-md {
				width: 100%;
			}
			.header-title {
				position: relative;
				padding: 3rem 1.5rem;
				background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0.8), transparent 85%);
				box-shadow: 0 0 2rem rgba(255, 255, 255, 0.3);
				border-radius: 50%;
				text-align: center;
				width: fit-content;
				max-width: 100%;
				margin: 0 auto;
				&-content {
					position: relative;
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
			.image-wrapper {
				position: relative;
				width: 80%;
				height: auto;
				aspect-ratio: 4/6;
				margin: 0 auto;
				align-self: flex-end;
				.header-background {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
				.image-element {
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					width: 90%;
					height: 93%;
					.header-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}

[data-anim="crowd"].has-animate {
	animation-name: crowd;
	animation-duration: 5000ms !important;
	animation-timing-function: linear !important;
	transform-origin: 20% 50%;
}

@keyframes crowd {
	from {
		transform: translate3d(-20%, 10%, 0) scale(0.7);
	}
	to {
		transform: translateZ(0) scaleZ(0);
	}
}
