.couple-section {
	position: relative;
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin-bottom: 1rem;
		z-index: 3;
		span {
			position: relative;
			font-size: 32px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
	}
	.couple-body {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: stretch;
		flex-wrap: wrap;
	}
	.couple-content {
		position: relative;
		margin-bottom: 3rem;
		padding: 5rem 3rem 3rem;
		@include max-md {
			padding: 0;
		}
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background-color: var(--color-primary);
			border-radius: var(--border-radius);
			opacity: 0.5;
			content: "";
		}
		.couple-content-body {
			position: relative;
			margin-bottom: 2rem;
			display: flex;
			justify-content: center;
			@include max-md {
				padding: 3rem 2rem 2rem;
				margin-bottom: 0;
			}
		}
		.image-wrapper {
			position: relative;
			width: 100%;
			height: auto;
			aspect-ratio: 7/8;
			.couple-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: var(--border-radius);
			}
		}
	}
	.couple {
		position: relative;
		text-align: center;
		width: 50%;
		@include max-md {
			width: 100%;
		}
		.image-wrap {
			width: 100%;
			position: relative;
			aspect-ratio: 7/8;
			margin: 0 auto;
			@include max-md {
				aspect-ratio: 5/6;
			}
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				&::after {
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					background-color: rgba($color: #000000, $alpha: 0.3);
					content: "";
				}
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
				}
			}
		}
		.couple-description {
			position: absolute;
			bottom: 3rem;
			left: 5rem;
			color: var(--color-light);
			text-align: left;
			max-width: 80%;
			z-index: 5;
			@include max-lg {
				left: 2rem;
				bottom: 2rem;
			}
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			.sosmed {
				color: var(--color-light);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
